@font-face {
  font-family: "Thicker";
  src: url("../fonts/thicker/Thicker-Regular-trial.ttf") format("truetype");
  font-weight: normal
}

@font-face {
  font-family: "Thicker";
  src: url("../fonts/thicker/Thicker-Regular-Italic-trial.ttf") format("truetype");
  font-style: italic;
}

*{
  font-family: 'Thicker', 'Verdana';
  /* box-sizing: content-box; */
  user-select: none;
}

body{
  background: #53B8B4;
  /* background: linear-gradient(90deg, rgba(83,184,180,1) 0%, rgba(123,214,210,1) 50%); */
}

html,body {
  height: 100%;
}

.container{
  position: fixed;
  height: 100%;
  top: 0;
  bottom: 0;
  margin: 0;
  padding-left: 60px;
  display: flex;
    align-items: center;
}

h1{
  position: relative;
  display: block;
  margin: 0;
  padding: 0;
  font-size: 7rem;
  color: white;
  font-weight: bold;
  height: 110px;
}

.badge{
  font-size: 32px;
    position: absolute;
    left: 0px;
    top: -56px;
}

p{
  position: relative;
  display: block;
  margin: 0;
  padding: 0;
  margin-top: 10px;
  padding-left: 6px;
  font-size: 18px;
  font-weight: 100;
  color: white;
}

i{margin-right: 5px;}

a{
 text-decoration: none;
 color: white;
 transition-duration: .4s;
}